<template>
  <div class="container">
    <div class="form">
      <el-form label-width="160px" :model="queryData" :rules="rules" :inline="true" ref="queryForm">
        <el-form-item label="业务代码分类名称" prop="bizTypeName">
          <el-input v-model="queryData.bizTypeName"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button plain size="small" type="primary" @click="query">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="data">
      <div class="table">
        <el-table style="width: 100%" :data="data" stripe :header-cell-style="tableHeader" @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column prop="eName" label="英文名称" min-width="120">
            <template slot-scope="scope">
              <el-input size="small"
                        v-if="scope.row.isEdit"
                        style="width: 100%"
                        v-model="scope.row.eName"
                        placeholder="请输入英文名称">
              </el-input>
              <span v-else>{{scope.row.eName}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="cName" label="中文名称" min-width="120">
            <template slot-scope="scope">
              <el-input size="small"
                        v-if="scope.row.isEdit"
                        style="width: 100%"
                        v-model="scope.row.cName"
                        placeholder="请输入中文名称">
              </el-input>
              <span v-else>{{scope.row.cName}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="isFlag" label="是否标志类" min-width="120">
            <template slot-scope="scope">
              <el-select v-if="scope.row.isEdit" v-model="scope.row.isFlag" placeholder="请选择是否标志类">
                <el-option label="是" value="是"></el-option>
                <el-option label="否" value="否"></el-option>
              </el-select>
              <span v-else>{{scope.row.isFlag}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="valType" label="业务代码类型" min-width="120">
            <template slot-scope="scope">
              <el-select v-if="scope.row.isEdit" v-model="scope.row.valType" placeholder="请选择业务代码类型">
                <el-option label="枚举型" value="枚举型"></el-option>
                <el-option label="连续型分段" value="连续型分段"></el-option>
              </el-select>
              <span v-else>{{scope.row.valType}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="isOrgSpecial" label="是否机构个性化代码" min-width="120">
            <template slot-scope="scope">
              <el-select v-if="scope.row.isEdit" v-model="scope.row.isOrgSpecial" placeholder="请选择是否机构个性化代码">
                <el-option label="是" value="是"></el-option>
                <el-option label="否" value="否"></el-option>
              </el-select>
              <span v-else>{{scope.row.isOrgSpecial}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="checkTab" label="校验数据表" min-width="120">
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="150">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="设置校验数据表" placement="top">
                <el-button
                  size="small"
                  type="text"
                  @click="editCheck(scope.$index)"
                  icon="iconfont icon-bianji">
                </el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="删除" placement="top">
                <el-button
                  @click.native.prevent="removeRecord(scope.row.no)"
                  type="text"
                  size="small"
                  icon="iconfont icon-iconfont-shanchu"
                  style="color: #FE2B6F">
                </el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-button
        :style="specialTableBtn"
        type="primary"
        icon="el-icon-plus"
        @click="add">
        新增行
      </el-button>
    </div>
    <div class="option">
      <el-button  type="primary" size="small" plain @click="edit">编辑</el-button>
      <el-button  type="primary" size="small" plain @click="save">暂存</el-button>
      <el-button  type="primary" size="small" plain>导入</el-button>
      <el-button  type="primary" size="small" plain>导出</el-button>
      <el-button  type="primary" size="small" plain>发布</el-button>
    </div>
    <el-dialog title="设置校验数据表" :visible.sync="treeVisible" width="40%">
      <el-tree
        ref="tree"
        :data="treeData"
        show-checkbox
        node-key="id"></el-tree>
      <div class="option" slot="footer">
        <el-button type="primary" @click="sure">确定</el-button>
        <el-button type="primary" @click="cancel">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {generateUUID} from '@/utils/uuid'
export default {
  name: 'BizCodeType',
  data () {
    return {
      queryData: {
        bizTypeName: ''
      },
      rules: {
        bizTypeName: [{required: true, trigger: 'blur', message: '查询条件不能为空'}]
      },
      specialTableBtn: {
        width: '100%',
        marginTop: '5px',
        border: '1px dashed #15afff',
        background: '#fff',
        color: '#15afff',
        display: 'block'
      },
      data: [
        {no: '01', eName: 'usePro', cName: '使用性质', isFlag: '是', valType: '枚举型', isOrgSpecial: '否', checkTab: '', isEdit: false},
        {no: '02', eName: 'carType', cName: '车辆种类', isFlag: '是', valType: '枚举型', isOrgSpecial: '是', checkTab: '', isEdit: false},
        {no: '03', eName: 'comLastAmount', cName: '商业险上年出险金额', isFlag: '是', valType: '连续型分段', isOrgSpecial: '否', checkTab: '', isEdit: false}
      ],
      multipleSelection: [],
      treeVisible: false,
      currentIndex: 0,
      treeData: [
        {
          id: '1',
          label: '车辆信息表',
          children: [
            {id: '1.1', label: '使用性质', value: '车辆信息表.使用性质'},
            {id: '1.2', label: '车辆种类', value: '车辆信息表.车辆种类'},
            {id: '1.3', label: '车辆颜色', value: '车辆信息表.车辆颜色'}
          ]
        },
        {
          id: '2',
          label: '商业险信息表',
          children: [
            {id: '2.1', label: '保费金额', value: '商业险信息表.保费金额'},
            {id: '2.2', label: '出险次数', value: '商业险信息表.出险次数'},
            {id: '2.3', label: '出险金额', value: '商业险信息表.出险金额'}
          ]
        }
      ]
    }
  },
  methods: {
    query () {
      this.$refs['queryForm'].validate(valid => {
        if (valid) {
          if (this.queryData.bizTypeName === '使用性质') {
            this.data = [
              {no: '01', eName: 'usePro', cName: '使用性质', isFlag: '是', valType: '枚举型', isOrgSpecial: '否', checkTab: '', isEdit: false}
            ]
          } else if (this.queryData.bizTypeName === '车辆种类') {
            this.data = [
              {no: '02', eName: 'carType', cName: '车辆种类', isFlag: '是', valType: '枚举型', isOrgSpecial: '是', checkTab: '', isEdit: false}
            ]
          } else if (this.queryData.bizTypeName === '商业险上年出险金额') {
            this.data = [
              {no: '03', eName: 'comLastAmount', cName: '商业险上年出险金额', isFlag: '是', valType: '连续型分段', isOrgSpecial: '否', checkTab: '', isEdit: false}
            ]
          }
        }
      })
    },
    handleSelectionChange (vals) {
      this.multipleSelection = []
      vals.forEach(val => {
        this.multipleSelection.push(val.no)
      })
    },
    edit () {
      if (this.multipleSelection.length > 0) {
        this.data.forEach(item => {
          if (this.multipleSelection.includes(item.no)) {
            item.isEdit = true
          }
        })
      }
    },
    save () {
      this.data.forEach(item => {
        item.isEdit = false
      })
    },
    editCheck (index) {
      this.currentIndex = index
      this.treeVisible = true
    },
    sure () {
      let temp = ''
      // 遍历选中节点，若为叶子结点，则拼接值
      this.$refs['tree'].getCheckedNodes().forEach(node => {
        if (node.children === undefined) {
          temp += node.value + ','
        }
      })
      temp = temp.substring(0, temp.length - 1)
      this.data[this.currentIndex].checkTab = temp
      this.cancel()
    },
    cancel () {
      this.$refs['tree'].setCheckedKeys([])
      this.treeVisible = false
      this.currentIndex = 0
    },
    add () {
      const no = generateUUID()
      console.log(no)
      const newItem = {no: no, eName: '', cName: '', isFlag: '', valType: '', isOrgSpecial: '', checkTab: '', isEdit: true}
      this.data.push(newItem)
    },
    removeRecord (no) {
      this.$confirm('确定删除此条数据记录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const index = this.data.findIndex(item => item.no === no)
        this.data.splice(index, 1)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  },
  computed: {
    ...mapState({tableHeader: state => state.app.tableHeader})
  }
}
</script>

<style scoped>

</style>



